@use "global" as *;

.swiper-wrapper {
  width: 100%;
  aspect-ratio: calc(557 / 389);

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
}

.swiper-pagination-bullets.swiper-pagination-horizontal {
  top: 0;
  padding-top: calc(261 / 375 * 100% - rem(3));

  @include mq("md") {
    top: 50%;
    padding-top: calc(389 / 1100 * 50% + rem(8));
    width: calc(557 / 1100 * 100%);
  }
}

.swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 rem(6);
}

.swiper-pagination-bullet {
  width: rem(8);
  height: rem(8);
  background: #c4c4c4;
  opacity: 1;

  &-active {
    background: $color-yellow-dark;
  }
}
