@use "global" as *;

.main {
  display: block;
  width: 100%;
  height: auto;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    display: block;
    top: -1%;
    left: 0;
    width: 100%;
    height: 19.5%;
    background-image: linear-gradient(to right top, transparent 50%, $color-yellow-dark calc(50% + 1px), $color-yellow-dark calc(50% + 2px), transparent calc(50% + 3px));
    z-index: map-get($layer, bg) - 2;

    @include mq('md') {
      top: -3.6%;
      height: 44.5%;
    }
  }

  &::after {
    content: "";
    position: absolute;
    display: block;
    top: 19.5%;
    left: 0;
    width: 100%;
    height: 39.5%;
    background-image: linear-gradient(to right bottom, transparent 50%, $color-yellow-dark calc(50% + 1px), $color-yellow-dark calc(50% + 2px), transparent calc(50% + 3px));
    z-index: map-get($layer, bg) - 1;

    @include mq('md') {
      top: 40.5%;
      height: 44.6%;
    }
  }
}

