@use "global" as *;

.page-head {
  margin-bottom: rem(116);

  &__titile {
    font-size: rem(35);
    line-height: rem(35);
    letter-spacing: rem(2);
    font-weight: $bold;
    text-align: center;
    padding: rem(164) 0 rem(171);
    position: relative;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: rem(370);
    z-index: map-get($layer, bg);
  }

  &__links {
    margin-top: rem(12);
    letter-spacing: rem(1);
  }
}

