@use "global" as *;

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: rem(50);
  line-height: rem(50);
  z-index: map-get($layer, header);
  background: rgba(17, 17, 17, 0.5);
  transition: all .3s;

  &--opacity-offset {
    background: $color-black;
  }

  &__inner {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    padding-left: rem(22);
    display: inline-block;
    display: flex;
    justify-content: center;
    align-items: center;

    @include mq("md") {
      padding-left: rem(32);
    }
  }

  &__logo__link {
    font-family: $logo-font-family;
    font-size: rem(20);
    letter-spacing: rem(1.2);

    @include mq("md") {
      font-size: rem(30);
      line-height: rem(50);
    }
  }

  &__icon {
    padding-right: rem(22);
  }

  &__list {

    @include mq('md') {
      display: flex;
      justify-content: flex-end;
    }
  }

  &__item {
    & + & {
      margin-left: rem(30);
      letter-spacing: rem(2);
    }
  }

  &__link {
    letter-spacing: rem(2);
    line-height: rem(50);
    display: inline-block;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: rem(6);
      left: 0;
      width: 100%;
      height: rem(1);
      background: $color-yellow-light;
      transition: opacity .3s;
      opacity: 0;
    }

    &:hover,
    &:focus {
      &::after {
        opacity: 1;
      }
    }


    &--contact {
      padding: 0 rem(31);
      background: $color-white;
      color: $color-main;
      transition: .3s;

      &::after {
        display: none;
      }
      
      &:hover, 
      &:focus {
        background: $color-main;
        color: $color-white;
        text-decoration: none;
        opacity: 1;
      }
    }
  }
}


