@use "global" as *;

.modal {

  &__bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: $color-black;
    z-index: map-get($layer, modal);
    display: none;
  }

  &__list {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    text-align: center;
    display: block;
    z-index: map-get($layer, modal) + 1;
    display: none;
  }

  &__item {
    width: 100%;

    & + & {
      margin-top: rem(32);
    }
  }
}
