@use "global" as *;

.footer {
  padding: rem(50) 0 rem(59);
  text-align: center;
  letter-spacing: rem(1.2);

  @include mq('md') {
    padding: rem(34) 0;
  }

  &__inner {
    width: calc(100% - rem(64));
    margin: 0 auto;

    @include mq('md') {
      display: flex;
      justify-content: space-between;
    }
  }

  &__logo {
    font-size: rem(20);
    font-family: $logo-font-family;
  }

  &__list {
    
    @include mq('md') {
      display: flex;
      justify-content: space-between;
      letter-spacing: rem(2);
    }
  }

  &__item {
    margin-top: rem(7);

    @include mq('md') {
      margin-top: rem(4);

      & + & {
        margin-left: rem(30);
      }
    }
  }

  &__link {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: rem(-10);
      left: 0;
      width: 100%;
      height: rem(1);
      background: $color-yellow-light;
      transition: opacity .3s;
      opacity: 0;
    }

    &:hover,
    &:focus {
      &::after {
        opacity: 1;
      }
    }
  }
  
  &__copyright {
    font-size: rem(11);
    margin-top: rem(32);

    @include mq('md') {
      font-size: rem(12);
      margin-top: rem(75);
    }
  }
}