@use "global" as *;

.news {
  margin-top: rem(22);

  @include mq("md") {
    margin-top: rem(32);
  }

  &__inner {
    @include mq("md") {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 rem(53);
    }
  }

  &__header {
    display: flex;
    align-items: center;
  }
  
  &__title {
    display: inline-block;
    width: rem(99);
    max-width: 100%;
    font-size: rem(14);
    font-weight: $medium;
    padding: rem(6) rem(4) rem(3);
    border: 1px solid $color-white;
    text-align: center;
    margin-left: rem(32);
  
    @include mq("md") {
      margin-left: rem(48);
    }
  }

  &__content-wrap {
    display: inline-block;
    text-align: center;
    margin-top: rem(16);
  
    @include mq("md") {
      text-align: left;
      margin-top: 0;
      flex: 1 1;
      padding-left: rem(60);
      padding-right: rem(50);
    }
  }

  &__content {
    text-align: left;

    &:hover,
    &:focus {
      text-decoration: underline;
    }

  }

  &__footer {
    text-align: center;
    margin-top: rem(28);

    @include mq("md") {
      margin-top: 0;
    }
  }

  &__button {
    @include mq("md") {
      color: $color-black;
      width: rem(134);
      padding: rem(3) rem(14);
      font-size: rem(16);
      border: none;
      letter-spacing: 0;
      line-height: inherit;
      background: linear-gradient(to right, $color-main 50%, $color-white 50%);
      background-size: 200% 100.5%;
      background-position: right bottom;

      &:hover,
      &:focus {
        background-position: left bottom;
        color: $color-white;
        opacity: 1;
      }
    }
  }
}
