@use "global" as *;

.contact {
  margin-top: rem(120);
  padding: rem(69) 0 rem(64);
  background: $color-main;

  @include mq('md') {
    margin-top: rem(150);
    padding: rem(144) 0 rem(148);
    text-align: center;
  }

  &__text {
    margin-top: rem(-31);
    letter-spacing: rem(2);
  }

  &__footer {
    margin-top: 47px;
    text-align: center;

    @include mq('md') {
      margin-top: rem(60);
      font-size: rem(20);
    }
  }


}