@use "global" as *;

.pagetop {
  position: fixed;
  bottom: rem(41);
  right: rem(21);
  z-index: map-get($layer, floating);

  @include mq('md') {
    bottom: rem(32);
    right: rem(31);
  }

  &__icon {
    display: block;
    width: rem(45);
    height: rem(45);
    background: $color-white;
    position: relative;
    cursor: pointer;
    z-index: inherit;
    transition: all .3s;

    &::after {
      position: absolute;
      display: block;
      content: "";
      top: calc(50% + rem(6));
      left: 50%;
      transform: translate(-50%, -50%) rotate(45deg);
      width: rem(17);
      height: rem(17);
      border-top: rem(2) solid $color-main;
      border-left: rem(2) solid $color-main;
      z-index: inherit;
    }

    &:hover {
      background: $color-main;

      &::after {
        border-top: rem(2) solid $color-white;
        border-left: rem(2) solid $color-white;      }
    }
  }
}
