@use "global" as *;

.card {
  display: block;
  position: relative;
  z-index: map-get($layer, default) + 1;

  &:hover,
  &:focus {

    .card__body {
      background: $color-main;
      color: $color-white;
    }
  }

  &__figure {
    display: block;
    width: 100%;
    aspect-ratio: calc(350 / 233);
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__body {
    background: $color-white;
    color: $color-main;  
    letter-spacing: rem(1);
    line-height: rem(26);
    font-size: rem(14);
    padding: rem(4) rem(23) rem(16);
    transition: all .3s;
  }

  &__title {
    font-size: rem(18);
    font-weight: $medium;
    line-height: rem(35);
    
  }
  
  &__lead {
    margin-top: rem(3);
    
    @include mq('md') {
      margin-top: rem(6);
    }
  }
  
  &__data {
    display: flex;
    justify-content: space-between;
    margin-top: rem(6);
    
    @include mq('md') {
      margin-top: rem(10);
    }
  }

  &__label {
    background: $color-yellow-light;
    padding: 0 rem(4);
    color: $color-black;
  }
}

.card--new {
  position: relative;
  padding-top: rem(16);

  @include mq('md') {
    padding-top: rem(4);
  }

  
  &::after {
    content :"New";
    position: absolute;
    left: 0;
    top: 0;
    transform: translateX(rem(-14));
    line-height: rem(35);
    letter-spacing: rem(2);
    padding: rem(12) rem(10);
    background: #FFBA6A;
    border-radius: 50%;
    display: block;
    font-size: rem(16);
    color: $color-main;

    @include mq('md') {
      top: rem(-15);
    }
  }
}
