@use "global" as *;

.section__title {
  font-size: rem(20);
  text-align: center;
  position: relative;
  letter-spacing: rem(2);
  margin-bottom: rem(48);

  @include mq('md') {
    font-size: rem(30);
    margin-bottom: rem(34);
    letter-spacing: rem(0.5);
  }

  &::after {
    bottom: rem(4);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 50px;
    font-family: $second-font-family;
    color: $color-yellow-dark;
    opacity: 0.2;
    z-index: map-get($layer, bg) + 1;

    @include mq('md') {
      font-size: rem(110);
      left: 0;
      transform: translateX(0);
      bottom: rem(-48);
      line-height: 1;
      letter-spacing: rem(2);
    }
  }

  &--content {
    
    @include mq('md') {
      margin-bottom: rem(42);
    }

    &::after {
      content: "Content";
    }
  }

  &--works {
    @include mq('md') {
    }

    &::after {
      content: "Works";
      
      @include mq('md') {
        left: auto;
        right: 0;
        bottom: rem(-42);
      }
    }
  }

  &--overview {
    @include mq('md') {
    }
    &::after {
      content: "Overview";
      @include mq('md') {
        bottom: rem(-42);
      }
    }
  }

  &--blog {
    &::after {
      content: "Blog";
      
    @include mq('md') {
      left: auto;
      right: 0;
      bottom: rem(-34);
    }
    }
  }

  &--contact {
    @include mq('md') {
      margin-bottom: rem(48);
    }
    &::after {
      content: "Contact";

      @include mq('md') {
        bottom: rem(-2);
      }
    }
  }
}
