@use "global" as *;

.page-content {
  &__group {
    margin-bottom: rem(42);
    display: flex;
    align-items: center;

    &__item {
      & + & {
        margin-left: rem(38);
      }
    }

    &__link {
      letter-spacing: rem(1);

      &--active {
        background: $color-white;
        padding: rem(3) rem(10) rem(4);
        color: $color-main;
      }
    }
  }

  &__index {
    margin-top: rem(104);
    display: flex;
    align-items: center;
    justify-content: center;

    &__item {
      &:first-child {
        margin-right: rem(24);
      }
      &:last-child {
        margin-left: rem(24);
      }
    }

    &__number-item {
      & + & {
        margin-left: rem(14);
      }
    }

    &__link {
      line-height: auto;
      letter-spacing: rem(2);
      padding: 0 rem(9);
      text-align: center;
      border: 1px solid $color-white;
    }

    &__number-link {
      line-height: rem(15.8);
      letter-spacing: rem(0.5);
      padding: 0 rem(7);
      text-align: center;
      border: 1px solid $color-white;

      &--active {
        background: $color-yellow-dark;
        border: 1px solid $color-yellow-dark;
        color: $color-main;
      }
    }
  
  }
}
