@use "global" as *;

.card-list {
  @include mq("md") {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: rem(-23) rem(-12);
  }

  &__item {
    & + & {
      margin-top: rem(24);
    }

    
    @include mq("md") {
      & + & {
        margin: rem(23) rem(12);
      }

      flex: 0 1 calc(1 / 3 * 100% - rem(72.1) / 3);
      margin: rem(23) rem(12);

    }
  }
}

