@use "global" as *;

.hamburger-icon {
  display: block;
  height: rem(2);
  width: rem(24);
  background: $color-white;
  position: relative;
  transition: transform .3s ease;

  &.is-open {
    height: 0;

    &::before {
      transform: translate(rem(-12), rem(5.5)) rotate(45deg)
    }

    &::after {
      transform: translate(rem(-12), rem(-5.5)) rotate(-45deg)
    }
  }

  &::before {
    content: "";
    position: absolute;
    top: rem(-6);
    left: 50%;
    transform: translateX(-50%);
    display: block;
    height: rem(2);
    width: rem(24);
    background: $color-white;
    transition: transform .3s ease;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: rem(-6);
    left: 50%;
    transform: translateX(-50%);
    display: block;
    height: rem(2);
    width: rem(24);
    background: $color-white;
    transition: transform .3s ease;
  }
}