@use "global" as *;

.media {
  position: relative;

  @include mq("md") {
    width: 100%;
    margin: 0;
    background: transparent;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    margin-right: calc(50% - 50vw);
    margin-left: calc(50% - 50vw);
    width: 100vw;
    height: 100%;
    background: $color-main;
    z-index: map-get($layer, bg);

    @include mq("md") {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      margin-right: calc(50% - 50vw);
      width: calc(896 / 1280 * 100vw);
      height: 100%;
      background: $color-main;
      z-index: map-get($layer, bg);
    }
  }

  &__header {
    overflow: hidden visible;
    width: 100vw;
    aspect-ratio: calc(557 / 389);
    margin: 0 calc(50% - 50vw);

    @include mq("md") {
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      width: calc(557 / 1100 * 100%);
      margin: 0;
    }
  }

  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

  &__content-wrap {
    text-align: center;
    padding: rem(24) 0 rem(21);
    letter-spacing: rem(2);

    @include mq("md") {
      width: calc(510 / 1100 * 100%);
      margin-left: auto;
      text-align: left;
      padding: rem(110) 0 rem(118);
    }
  }

  &__title {
    font-size: rem(20);
    letter-spacing: rem(2);

    @include mq("md") {
      font-size: rem(25);
      letter-spacing: rem(0.5);
    }
  }

  &__copy {
    text-align: left;
    line-height: rem(23.17);
    margin-top: rem(22);

    @include mq("md") {
      line-height: rem(35);
      letter-spacing: rem(0.5);
      margin-top: rem(36);
    }
  }

  &__footer {
    margin-top: rem(27);

    @include mq("md") {
      margin-top: rem(48);
    }
  }
}

// reverse
.media--pc-rev {
  &::after {
    @include mq("md") {
      content: "";
      right: auto;
      left: 0;
      margin-right: 0;
      margin-left: calc(50% - 50vw);
    }
  }

  .media__header {
    @include mq("md") {
      left: auto;
      right: 0;
      margin-left: auto;
    }
  }

  .media__content-wrap {
    @include mq("md") {
      margin-left: 0;
      padding: rem(110) 0 rem(118);
    }
  }
}
