@use "global" as *;

.content {
  margin-top: rem(151);

  @include mq("md") {
    margin-top: rem(190);
  }

  &__body {
    display: flex;
    flex-wrap: wrap;
    width: 100vw;
    margin-right: calc(50% - 50vw);
    margin-left: calc(50% - 50vw);

    @include mq("md") {
      flex-wrap: nowrap;
    }
  }

  &__link {
    display: block;
    width: 50%;
    aspect-ratio: calc(188 / 168);
    position: relative;

    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: $color-white;
      transition: all 0.3s;
      opacity: 0;
    }

    &:hover,
    &:focus {
      &::after {
        opacity: 0.6;
      }
    }

    @include mq("md") {
      width: 25%;
      aspect-ratio: 1 / 1;
    }

    &__img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }

    &__title {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: rem(20);
      letter-spacing: rem(2);
      width: 100%;
      text-align: center;
      z-index: map-get($layer, default) + 1;

      @include mq("md") {
        font-size: rem(25);
        letter-spacing: rem(0.5);

        &--long {
          letter-spacing: rem(2);
        }
      }
    }
  }
}
